import React from 'react';
import Container from '@/screen/Container';
import { BlockComponent, Title } from '@/components/SubComponent';

class Extend_view extends React.Component {
    constructor() {
        super()
        this.state = {
            data: [
                {
                    route: '/Extend_art1',
                    desc: 'prototypeprotoconstructor'
                }, {
                    route: '/Extend_art2',
                    desc: 'javascript'
                }, {
                    route: '/Extend_art3',
                    desc: 'javascript5'
                }, {
                    route: '/Extend_art4',
                    desc: 'javascript3'
                }, {
                    route: '/Extend_art5',
                    desc: 'javascript6'
                }
            ]
        }
    }

    render() {
        return(
            <Container>
                <Title title={''} />
                {
                    this.state.data.map((item, index) => {
                        return (<BlockComponent key={index} route={item.route} title={item.desc}></BlockComponent>)
                    })
                }
            </Container>
        )
    }
}

export default Extend_view;