import React from 'react';
import Container from '@/screen/Container';
import { Title, Paragraph, CodeBlock } from '@/components/SubComponent';

class Sort_art7 extends React.Component {
    render() {
        return(
            <Container>
                <div style={{padding: '0 10px'}}>
                    <Title title={'Heap Sort'} />
                    <Paragraph title={'Heapsort'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'(R1,R2.Rn)'} />
                    <Paragraph title={'R[1]R[n](R1,R2,Rn-1)(Rn),R[1,2n-1]<=R[n]'} />
                    <Paragraph title={'R[1](R1,R2,Rn-1)R[1](R1,R2.Rn-2)(Rn-1,Rn)n-1'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <img style={{width: '100%'}} src={require('@/assets/images/JS_sort_art9.gif')} alt="png"/>
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
<CodeBlock content={
`var len; // len
function buildMaxHeap(arr) { // 
    len = arr.length;
    for (var i = Math.floor(len / 2); i >= 0; i--) {
        heapify(arr, i);
    }
}
function heapify(arr, i) { // 
    var left = 2 * i + 1,
        right = 2 * i + 2,
        largest = i;
    if (left < len && arr[left] > arr[largest]) {
        largest = left;
    }
    if (right < len && arr[right] > arr[largest]) {
        largest = right;
    }
    if (largest != i) {
        swap(arr, i, largest);
        heapify(arr, largest);
    }
}
function swap(arr, i, j) {
    var temp = arr[i];
    arr[i] = arr[j];
    arr[j] = temp;
}
function heapSort(arr) {
    buildMaxHeap(arr);
    for (var i = arr.length - 1; i > 0; i--) {
        swap(arr, 0, i);
        len--;
        heapify(arr, 0);
    }
    return arr;
}
`} />
                </div>
            </Container>
        )
    }
}

export default Sort_art7;