import React from 'react';
import Container from '@/screen/Container';
import { Title, Paragraph, CodeBlock } from '@/components/SubComponent';

class Sort_art3 extends React.Component {
    render() {
        return(
            <Container>
                <div style={{padding: '0 10px'}}>
                    <Title title={'Insertion Sort'} />
                    <Paragraph title={'Insertion-Sort'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'in-place'} />
                    <Paragraph title={''} />
                    <Paragraph title={''} />
                    <Paragraph title={''} />
                    <Paragraph title={'3'} />
                    <Paragraph title={''} />
                    <Paragraph title={'2~5'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <img style={{width: '100%'}} src={require('@/assets/images/JS_sort_art5.gif')} alt="png"/>
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
<CodeBlock content={
`function insertionSort(arr) {
    var len = arr.length;
    var preIndex, current;
    for (var i = 1; i < len; i++) {
        preIndex = i - 1;
        current = arr[i];
        while (preIndex >= 0 && arr[preIndex] > current) {
            arr[preIndex + 1] = arr[preIndex];
            preIndex--;
        }
        arr[preIndex + 1] = current;
    }
    return arr;
}
`} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'in-placeO(1)'} />
                </div>
            </Container>
        )
    }
}

export default Sort_art3;