<CodeBlock content={
`//
setCityData: function(data) {
  data = data.cities;
  let citys = {}, sortCitys = [];
  let k, gname, name, i, tmp = {}, index;

  //nameK
  for (k in data) {
    name = data[k].name;
    if (!name) {
      continue;
    }
    gname = name[0].toUpperCase();
    if (!citys[gname]) citys[gname] = [];
    citys[gname].push(data[k]);
  }

  for (i = 65; i < 91; i++) {
    tmp = {};
    tmp[String.fromCharCode(i)] = [];
    sortCitys.push(tmp);
  }

  for (k in citys) {
    index = k.charCodeAt() - 65;
    tmp = {};
    tmp[k] = citys[k];
    sortCitys[index] = tmp;
  }

  this.setData({
    cityData: sortCitys,
    isCityShow: ''
  });
},
`} />
