<CodeBlock content={
`//pageData,mod,
initPage(pageData, mod) {
  //debugger;
  let _pageData = {};
  let key, value, k, v;

  //
  Object.assign(_pageData, this.getPageFuncs(), pageData);

  //
  _pageData.data = {};
  Object.assign(_pageData.data, this.getPageData(), pageData.data || {});

  for( key in mod) {
    value = mod[key];
    for(k in value) {
      v = value[k];
      if(k === 'data') {
        Object.assign(_pageData.data, v);
      } else {
        _pageData[k] = v;
      }
    }
  }

  console.log(_pageData);
  return _pageData;
}
`} />
