import React from 'react';
import Container from '@/screen/Container';
import { Title, Paragraph, CodeBlock } from '@/components/SubComponent';

class Sort_art8 extends React.Component {
    render() {
        return(
            <Container>
                <div style={{padding: '0 10px'}}>
                    <Title title={'Counting Sort'} />
                    <Paragraph title={' '} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={''} />
                    <Paragraph title={'iCi'} />
                    <Paragraph title={'C'} />
                    <Paragraph title={'iC(i)C(i)1'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <img style={{width: '100%'}} src={require('@/assets/images/JS_sort_art10.gif')} alt="png"/>
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
<CodeBlock content={
`function countingSort(arr, maxValue) {
    var bucket = new Array(maxValue + 1),
        sortedIndex = 0;
    arrLen = arr.length,
        bucketLen = maxValue + 1;
    for (var i = 0; i < arrLen; i++) {
        if (!bucket[arr[i]]) {
            bucket[arr[i]] = 0;
        }
        bucket[arr[i]]++;
    }
    for (var j = 0; j < bucketLen; j++) {
        while (bucket[j] > 0) {
            arr[sortedIndex++] = j;
            bucket[j]--;
        }
    }
    return arr;
}
`} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={' n  0 k O(n+k)O(n+k)k'} />
                </div>
            </Container>
        )
    }
}

export default Sort_art8;