import React from 'react';
import Container from '@/screen/Container';
import { BlockComponent, Title } from '@/components/SubComponent';

class Range_view extends React.Component {
    constructor() {
        super()
        this.state = {
            data: [
                {
                    route: 'Range_art1',
                    desc: 'javascript'
                }, {
                    route: 'Range_art2',
                    desc: 'javascript'
                }, {
                    route: '/Range_art3',
                    desc: 'javascript(hoisting)'
                }, {
                    route: '/Range_art4',
                    desc: 'javascript'
                }, {
                    route: '/Range_art5',
                    desc: 'javascript'
                }
            ]
        }
    }

    render() {
        return(
            <Container>
                <Title title={''} />
                {
                    this.state.data.map((item, index) => {
                        return (<BlockComponent key={index} route={item.route} title={item.desc}></BlockComponent>)
                    })
                }
            </Container>
        )
    }
}

export default Range_view;