import React, { Component } from 'react';
import Container from '@/screen/Container';
import '@/assets/style/level2_style.less';
import { Accordion, List } from 'antd-mobile';
import { Link } from 'react-router-dom';

class Frame_view extends Component {
    constructor(props) {
        super(props)
        this.state = {
            vueList: [
                {title: 'Vue', path: '/'},
                {title: 'Vue',  path: '/'},
                {title: 'Vue',  path: '/'},
                {title: 'Axios',  path: '/'},
                {title: 'vue-router',  path: '/'},
                {title: 'vuex',  path: '/'}
            ],
            reactList: [
                {title: 'react',  path: '/'},
                {title: 'react-router',  path: '/'},
                {title: 'styled-component',  path: '/'},
                {title: 'fetch',  path: '/'},
                {title: 'redux',  path: '/React_redux'},
                {title: 'saga',  path: '/Redux_saga'}
            ]
        }
    }

    render() {
        return (
            <Container>
                <div className="level2">
                    <div className="_desc">
                        <h2></h2>
                        <p>Vuereactangular</p>
                        <p>MVVMAngular.jsReact.jsVue.jsDOMDOM</p>
                        <p>ReactVueHTMLReactJSVueHTMLReactJSXJS</p>
                    </div>
                    <main>
                        <div>
                            <Accordion defaultActiveKey="0" className="my-accordion">
                                <Accordion.Panel header="Vue">
                                    <List className="my-list">
                                        {this.state.vueList.map((item, index) => {
                                            return (
                                                <List.Item key={index}>
                                                    <Link to={item.path}>{item.title}</Link>
                                                </List.Item>
                                            )
                                        })}
                                    </List>
                                </Accordion.Panel>
                            </Accordion>
                        </div>
                        <div>
                            <Accordion defaultActiveKey="0" className="my-accordion">
                                <Accordion.Panel header="react">
                                    <List className="my-list">
                                        {this.state.reactList.map((item, index) => {
                                            return (
                                                <List.Item key={index}>
                                                    <Link style={{display: 'inline-block', width: '100%', height: '100%'}} to={item.path}>{item.title}</Link>
                                                </List.Item>
                                            )
                                        })}
                                    </List>
                                </Accordion.Panel>
                            </Accordion>
                        </div>
                    </main>
                </div>
            </Container>
        )
    }
}

export default Frame_view
