import React from 'react';
import Container from '@/screen/Container';
import { Title, Paragraph, CodeBlock } from '@/components/SubComponent';

class Sort_art6 extends React.Component {
    render() {
        return(
            <Container>
                <div style={{padding: '0 10px'}}>
                    <Title title={'Quick Sort'} />
                    <Paragraph title={''} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'listsub-lists'} />
                    <Paragraph title={' pivot'} />
                    <Paragraph title={'partition'} />
                    <Paragraph title={'recursive'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <img style={{width: '100%'}} src={require('@/assets/images/JS_sort_art8.gif')} alt="png"/>
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
<CodeBlock content={
`function quickSort(arr, left, right) {
    var len = arr.length,
        partitionIndex,
        left = typeof left != 'number' ? 0 : left,
        right = typeof right != 'number' ? len - 1 : right;
    if (left < right) {
        partitionIndex = partition(arr, left, right);
        quickSort(arr, left, partitionIndex - 1);
        quickSort(arr, partitionIndex + 1, right);
    }
    return arr;
}
function partition(arr, left ,right) { // 
    var pivot = left, // pivot
        index = pivot + 1;
    for (var i = index; i <= right; i++) {
        if (arr[i] < arr[pivot]) {
            swap(arr, i, index);
            index++;
        }
    }
    swap(arr, pivot, index - 1);
    return index - 1;
}
function swap(arr, i, j) {
    var temp = arr[i];
    arr[i] = arr[j];
    arr[j] = temp;
}
`} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'""QuicksortC. A. R. Hoare1934--1960'} />
                    <Paragraph title={<p><a href="http://www.ruanyifeng.com/blog/2011/04/quicksort_in_javascript.html"></a></p>} />
                </div>
            </Container>
        )
    }
}

export default Sort_art6;