import React from 'react';
import Container from '@/screen/Container';
import { BlockComponent, Title } from '@/components/SubComponent';

class Sprogram_view extends React.Component {
    constructor() {
        super()
        this.state = {
            data: [
                {
                    route: '/Sprogram_art1',
                    desc: '(1)'
                }, {
                    route: '/Sprogram_art2',
                    desc: '(2)'
                }, {
                    route: '/Sprogram_art3',
                    desc: '(3)'
                }, {
                    route: '/Sprogram_art4',
                    desc: '(4)UI'
                }, {
                    route: '/Sprogram_art5',
                    desc: '(5)'
                }, {
                    route: '/Sprogram_art6',
                    desc: '(6)'
                }, {
                    route: '/Sprogram_art7',
                    desc: '(7)'
                }
            ]
        }
    }

    render() {
        return(
            <Container>
                <Title title={''} />
                {
                    this.state.data.map((item, index) => {
                        return (<BlockComponent key={index} route={item.route} title={item.desc}></BlockComponent>)
                    })
                }
            </Container>
        )
    }
}

export default Sprogram_view;