import React from 'react';
import Container from '@/screen/Container';
import { BlockComponent, Title } from '@/components/SubComponent';

class Sort_view extends React.Component {
    constructor() {
        super()
        this.state = {
            data: [
                {
                    route: '/Sort_art1',
                    desc: 'Bubble Sort'
                }, {
                    route: '/Sort_art2',
                    desc: 'Selection Sort'
                }, {
                    route: '/Sort_art3',
                    desc: 'Insertion Sort'
                }, {
                    route: '/Sort_art4',
                    desc: 'Shell Sort'
                }, {
                    route: '/Sort_art5',
                    desc: 'Merge Sort'
                }, {
                    route: '/Sort_art6',
                    desc: 'Quick Sort'
                }, {
                    route: '/Sort_art7',
                    desc: 'Heap Sort'
                }, {
                    route: '/Sort_art8',
                    desc: 'Counting Sort'
                }, {
                    route: '/Sort_art9',
                    desc: 'Bucket Sort'
                }, {
                    route: '/Sort_art10',
                    desc: 'Radix Sort'
                }
            ]
        }
    }

    render() {
        return(
            <Container>
                <Title title={''} />
                {
                    this.state.data.map((item, index) => {
                        return (<BlockComponent key={index} route={item.route} title={item.desc}></BlockComponent>)
                    })
                }
            </Container>
        )
    }
}

export default Sort_view;