import React from 'react';
import Container from '@/screen/Container';
import { Title, Paragraph, CodeBlock } from '@/components/SubComponent';

class Sort_art2 extends React.Component {
    render() {
        return(
            <Container>
                <div style={{padding: '0 10px'}}>
                    <Title title={'Selection Sort'} />
                    <Paragraph title={'(Selection-sort)'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'nn-1'} />
                    <Paragraph title={'R[1..n]'} />
                    <Paragraph title={'i(i=1,2,3n-1)R[1..i-1]R(i..n- R[k]1RR[1..i]R[i+1..n)11'} />
                    <Paragraph title={'n-1'} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <img style={{width: '100%'}} src={require('@/assets/images/JS_sort_art4.gif')} alt="png"/>
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
<CodeBlock content={
`function selectionSort(arr) {
    var len = arr.length;
    var minIndex, temp;
    for (var i = 0; i < len - 1; i++) {
        minIndex = i;
        for (var j = i + 1; j < len; j++) {
            if (arr[j] < arr[minIndex]) {    // 
                minIndex = j;                // 
            }
        }
        temp = arr[i];
        arr[i] = arr[minIndex];
        arr[minIndex] = temp;
    }
    return arr;
} 
`} />
                    <Paragraph style={{textIndent: 0, fontWeight: 700}} title={''} />
                    <Paragraph title={'O(n2)'} />
                </div>
            </Container>
        )
    }
}

export default Sort_art2;